.headerBox {
  padding: 1em 3em 1em 1em;
  width: 100%;
  background-color: #FFF;
  display: flex;
  align-items: flex-end; }
  .headerBox__logo {
    font-size: 36px;
    padding: 1em;
    color: #000;
    width: 240px; }
  .headerBox__gnav {
    margin: 0 0 0 auto;
    width: 480px; }
    .headerBox__gnav-lists {
      display: flex; }
      .headerBox__gnav-lists > li {
        padding: 20px 0;
        width: calc(100% / 3);
        text-align: center;
        position: relative; }
        .headerBox__gnav-lists > li > a {
          display: block;
          width: 100%;
          height: 100%;
          text-align: center;
          font-size: 20px;
          text-decoration: none;
          color: #000;
          border-right: solid 1px #000; }
          .headerBox__gnav-lists > li > a:last-child {
            border: none; }
          .headerBox__gnav-lists > li > a:hover {
            cursor: pointer; }
        .headerBox__gnav-lists > li .circleDeco {
          position: absolute;
          top: -120%;
          left: 50%;
          width: 0.5em;
          height: 0.5em;
          border-radius: 50%;
          background-color: #000;
          transform: translate3d(-50%, -50%, 1px);
          transition: top 0.4s ease-in, transform 0.6s ease-in; }
        .headerBox__gnav-lists > li .circleDeco-active {
          top: 50%;
          transform: scale(0, 0); }
        .headerBox__gnav-lists > li .menu {
          position: absolute;
          border: solid 1px #CCC;
          top: 122%;
          background-color: #FFF;
          width: 100%;
          max-height: 0;
          transition: 0.3s ease-in;
          overflow: hidden; }
          .headerBox__gnav-lists > li .menu-ok {
            max-height: 300px; }
          .headerBox__gnav-lists > li .menu > li {
            width: 100%;
            border-bottom: solid 1px #CCC;
            padding: 0; }
            .headerBox__gnav-lists > li .menu > li a {
              color: #111;
              padding: 0.4em 0;
              display: block;
              font-size: 20px;
              text-decoration: none; }
              .headerBox__gnav-lists > li .menu > li a:hover {
                background-color: #c7e8e1; }

.mainVis {
  padding: 70px 0;
  background-color: #5ABCA7; }
  .mainVis_eyeArea {
    margin: 0 auto 50px;
    display: flex;
    justify-content: space-between;
    width: 360px; }
  .mainVis_eyeAreaR {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: solid 4px #FFF;
    position: relative; }
    .mainVis_eyeAreaR-right {
      position: absolute;
      background-color: #FFF;
      top: 50%;
      left: 50%;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      transition: .05s; }
  .mainVis_eyeAreaL {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: solid 4px #FFF;
    position: relative; }
    .mainVis_eyeAreaL-left {
      position: absolute;
      background-color: #FFF;
      top: 50%;
      left: 50%;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      transition: .05s; }
  .mainVis .mainVis_mouthArea {
    margin: 0 auto 40px;
    width: 200px;
    height: 107px;
    position: relative;
    transform-style: preserve-3d;
    transition-duration: .4s; }
    .mainVis .mainVis_mouthArea.on {
      transform: rotateX(-180deg); }
    .mainVis .mainVis_mouthArea .mouth_off {
      position: absolute;
      width: 100%;
      backface-visibility: hidden; }
      .mainVis .mainVis_mouthArea .mouth_off img {
        width: 100%; }
    .mainVis .mainVis_mouthArea .mouth_on {
      position: absolute;
      backface-visibility: hidden;
      width: 100%;
      transform: rotateX(180deg); }
      .mainVis .mainVis_mouthArea .mouth_on img {
        width: 100%; }
  .mainVis .mouth_judge {
    display: block;
    width: 300px;
    margin: 0 auto;
    padding: 10px;
    border: solid 2px #FFF;
    color: #FFF; }

.topic1__title {
  margin: 0 auto 40px;
  padding: 40px 0;
  font-size: 36px;
  width: 360px;
  text-align: center; }

.topic1 .swiper-container {
  margin: 0 auto 60px;
  padding: 0 240px;
  width: 1000px;
  height: 560px;
  position: relative; }

.topic1__contents {
  margin: 0 auto 30px;
  width: 500px;
  height: 500px;
  position: relative; }
  .topic1__contents-front {
    padding: 30px;
    width: 400px;
    height: 400px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
    background-color: #FFF;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0; }
    .topic1__contents-front .panel__title {
      margin-bottom: 12px;
      font-size: 24px; }
  .topic1__contents-back {
    display: flex;
    align-items: flex-end;
    padding: 30px;
    width: 400px;
    height: 400px;
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: #5ABCA7;
    z-index: 5; }
    .topic1__contents-back .panel__link {
      margin-left: auto;
      width: 140px;
      border: solid 1px #FFF; }
      .topic1__contents-back .panel__link a {
        display: block;
        padding: 10px 20px;
        text-decoration: none;
        color: #FFF;
        position: relative; }
        .topic1__contents-back .panel__link a::before {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          z-index: -1;
          background: #FFF;
          transform-origin: right top;
          transform: scale(0, 1);
          transition: transform .3s; }
        .topic1__contents-back .panel__link a::after {
          content: "";
          width: 20px;
          height: 20px;
          background: url("./img/ico_arrow_on.png") 0/contain;
          transform: translate(-50%, -50%);
          position: absolute;
          top: 50%;
          right: 5%; }
        .topic1__contents-back .panel__link a:hover {
          color: #5ABCA7; }
          .topic1__contents-back .panel__link a:hover::before {
            transform-origin: left top;
            transform: scale(1, 1); }
          .topic1__contents-back .panel__link a:hover::after {
            background: url("./img/ico_arrow_off.png") 0/contain; }

